'use client';
import React from 'react';
import TextField from '@mui/material/TextField';
import Stack from '@mui/material/Stack';
import MuiButton from '@mui/material/Button';
import Alert from '@mui/material/Alert';
import { validateEmail, validatePassword } from '@/features/auth/validators';

type Credentials = { email: string; password: string };
type Props = {
  onSubmit: (creds: Credentials) => Promise<void> | void;
};

export function LoginForm({ onSubmit }: Props) {
  const [email, setEmail] = React.useState('');
  const [password, setPassword] = React.useState('');
  const [errors, setErrors] = React.useState<{ email?: string; password?: string }>({});
  const [submitting, setSubmitting] = React.useState(false);

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    const nextErrors: { email?: string; password?: string } = {};
    if (!email) nextErrors.email = 'Required';
    else if (!validateEmail(email)) nextErrors.email = 'Invalid email';
    if (!password) nextErrors.password = 'Required';
    else if (!validatePassword(password)) nextErrors.password = 'Password must be at least 8 characters';
    setErrors(nextErrors);
    if (Object.keys(nextErrors).length > 0) return;
    try {
      setSubmitting(true);
      await onSubmit({ email, password });
    } finally {
      setSubmitting(false);
    }
  };

  return (
    <form onSubmit={handleSubmit} noValidate>
      <Stack spacing={2} sx={{ maxWidth: 360 }}>
        <TextField
          label="Email"
          type="email"
          value={email}
          onChange={(e) => setEmail(e.target.value)}
          error={Boolean(errors.email)}
          helperText={errors.email}
        />
        <TextField
          label="Password"
          type="password"
          value={password}
          onChange={(e) => setPassword(e.target.value)}
          error={Boolean(errors.password)}
          helperText={errors.password}
        />
        {(errors.email || errors.password) && <Alert severity="error">Please fix the errors</Alert>}
        <MuiButton type="submit" variant="contained" disabled={submitting}>
          Sign in
        </MuiButton>
      </Stack>
    </form>
  );
}


